
import { computed, toRefs } from "vue";

export default {
	name: "SwitchCardItem",
	props: {
		/**  switch */
		modelValue: Boolean,
		/**  */
		title: String,
		/**  */
		description: String,
		/**  switch */
		color: {
			type: String,
			default: "success"
		},
		/**   */
		nodata: Boolean,
		/** . */
		disabled: Boolean
	},
	emits: ["update:modelValue"],
	setup(props: any, { emit }: { emit: any })
	{
		const { modelValue } = toRefs(props);
		const modelSwitch = computed({
			get: () => modelValue.value,
			set: (val) => emit("update:modelValue", val)
		});

		return {
			modelSwitch
		};
	}
};
