
import { computed, toRefs } from "vue";
import { getFormatTime } from "@/utils/time";

export default {
	name: "InputCardItem",
	props: {
		/**  */
		value: [String, Number, BigInt],
		/**  */
		title: String,
		/**  */
		description: String,
		/** : text, time, temperature */
		type: {
			type: String,
			default: "text"
		},
		/**   */
		nodata: Boolean,
		/** . */
		disabled: Boolean
	},
	setup(props: any)
	{
		const { value, type, nodata } = toRefs(props);
		const textValue = computed(() =>
		{
			switch (type.value)
			{
				case "time":
					if (!nodata.value)
					{
						switch (typeof value.value)
						{
							case "number":
							case "bigint": return getFormatTime(value.value);
							case "string": return value.value;
						}
					}
					return "--:--:--";

				case "temperature":
					if (!nodata.value)
					{
						switch (typeof value.value)
						{
							case "number": return value.value.toFixed(1) + "C";
							case "string": return value.value + "C";
						}
					}
					return "-.-C";

				default:
					switch (typeof value.value)
					{
						case "number": return value.value.toFixed();
						case "string": return value.value;
					}
					return "";
			}
		});

		return {
			textValue
		};
	}
};
